import React,{usEffect} from 'react'

function FlashMessages({msgs}) {
    return (
        <div className="floating-alerts">
            {msgs.map((msg,index)=>{
                return (
                    <div 
                    key={index}
                    className="alert alert-success text-center floating-alert shadow-sm">
                        {msg} 
                    </div>
                )
            })}
        </div>
    )
}

export default FlashMessages
